// import logo from './logo.svg';
import 'react-notion/src/styles.css';
import './App.css';
import { NotionRenderer } from "react-notion";
import React from 'react';

const ids = {
  'c0462f944e4d47f687dffcbd6b94dc2e': 'OSM & Co',
  '62f675e9dca84b8488f8db38344e175e': 'Git Bash',
  '6902709e28bd485aab7b52dd316e6104': 'cv2',
}

function App() {
  const [blockMap, setBlockMap] = React.useState({});
  console.log(blockMap);

  let idx = 0
  let id = Object.keys(ids)[idx];

  (async function getStaticProps(id) {
    if (Object.keys(blockMap).length === 0){
      const data = await fetch(
        "https://notion-api.splitbee.io/v1/page/" + id
      ).then(res => res.json());
  
      console.log(data);
      setBlockMap(data);
    }
  })(id);


  return (
    <div className="App">
      <header className="App-header">
        <h1>{ids[id]}</h1>
        <NotionRenderer blockMap={blockMap} />
      </header>
    </div>
  );
}

export default App;
